:root {
  --title-font: "Pacifico", cursive;
  --pharagrap-font: "Patrick Hand", cursive;
  --color-separator: #bf2813;
}

body {
  background-color: white;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--title-font);
  font-weight: 600;
}

a {
  font-family: var(--pharagrap-font);
}

body p {
  font-family: var(--pharagrap-font);
  font-weight: 600;
  text-align: center;
}

figcaption {
  font-family: var(--pharagrap-font);
  text-align: center;
  font-size: 1rem;
}

/** Home Section */

.bannerHome {
  background-image: url("../assets/img/Hero.webp");
}

.heroTitle {
  font-family: var(--title-font);
  letter-spacing: 5px;
}

h2 {
  line-height: 1.25rem;
  letter-spacing: 0.2em;
}

h4 {
  font-size: 2rem;
  /* text-wrap: nowrap; */
  letter-spacing: 0.2rem;
}

.bannerHome p {
  font-family: var(--pharagrap-font);
  font-weight: 700;
  /* font-size: 2rem; */
}

.bannerHome a {
  font-family: var(--pharagrap-font);
}

button {
  font-family: var(--pharagrap-font), cursive;
  letter-spacing: 5px;
  /* font-size: 5em; */
}

p {
  font-family: "Amatic SC";
  font-size: x-large;
  line-height: 2.5rem;
  text-align: center;
}

/** Intro Section */

.separator {
  width: 16.438rem;
  height: 2rem;
}

/** Carousel Section */

.carouselContainer {
  background-color: #e0e0e0;
  background-image: url("/src/assets/products/CBPromo_xs.webp");
  background-position: center;
  background-size: cover;
  background-blend-mode: blur(20px)
}

.imgCarousel.loaded {
  display: block;
}

/* .imgCarousel {
  display: none;
} */

/** Products Details Section */

.productDetailsTitle h2 {
  font-family: var(--title-font);
}

/** Footer Section */

footer .footerContact {
  position: absolute;
  left: 7rem;
  bottom: -9.5rem;
}

/** Cart Section  */

#goCataloge {
  font-family: var(--pharagrap-font);
}

/** Mobile Menu Section  */

#navBar_mobileMenu {
  display: none;
  opacity: 0;
  font-family: var(--pharagrap-font);
  letter-spacing: 1.8px;
  font-size: 1.2rem;
  width: 100%;
  height: 100vh;
  z-index: 6;
  background-color: #000;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  transition-property: opacity;
  transition-duration: 0.7s;
  transition-timing-function: ease-in-out;
}

.mobileMenu_content {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 60%;
  background-color: #ffffff;
  z-index: 6;
}

#closeModalBtn {
  height: 42px;
  width: 42px;
  position: absolute;
  right: 0;
  top: 0;
  margin: 10px;
}

.links_menu {
  margin-top: 130px;
  margin-left: 50px;
  display: flex;
  flex-direction: column;
  gap: 50px;
}

/** WhatsApp Icon **/

.wtspIconContainer {
  position: fixed;
  z-index: 99;
  bottom: 0;
  right: 0.5rem;
  transform: translateY(-40%);
  opacity: 0;
  transition: opacity 0.5s ease-out, transform 0.5s ease-out;
}

.animated-icon.show {
  opacity: 1;
  transform: translate(-20%, -18px);
}

/* Small (sm) */

@media (min-width: 640px) and (max-width: 767px) {
  .mobileMenu_content {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 40%;
    background-color: #ffffff;
    z-index: 6;
    padding-left: 2rem;
  }

  footer .footerContact {
    position: absolute;
    left: 13.5rem;
    bottom: -20px;
  }
}

/* Medium (md) */

@media (min-width: 768px) and (max-width: 1023px) {
  footer .footerContact {
    position: absolute;
    left: 18rem;
    bottom: 0;
  }
}

/* Large (lg) */

@media (min-width: 1024px) and (max-width: 1279px) {
  footer {
    /* background-image: url('../assets/img/footerImg.png'); */
    /* height: 340px; */
    background-position: top;
    -o-object-fit: cover;
       object-fit: cover;
    background-repeat: no-repeat;
  }

  footer .footerContact {
    position: absolute;
    left: 28rem;
    bottom: 1rem;
  }
}

/* Extra Large (xl) */

@media (min-width: 1280px) and (max-width: 1399px) {
  footer .footerContact {
    position: absolute;
    left: 32rem;
    bottom: 1rem;
  }
}

/* Extra extra Large (xxl) */

@media (min-width: 1400px) {
  footer .footerContact {
    position: absolute;
    left: 38rem;
    bottom: 1rem;
  }
}

